import { useDispatch, useSelector } from "react-redux"
import { Link, Redirect } from "react-router-dom"
import InputPasswordToggle from "@components/input-password-toggle"
import { Button, Card, CardBody, CardTitle, Form, FormGroup, Label } from "reactstrap"
import "@styles/base/pages/page-auth.scss"
import UILoader from '@components/ui-loader'
import InputGroup from '@components/input-group'
import { signUp, signupChange } from '@store/SignUp/actions'

const SignUp = (props) => {
  const { authenticated } = useSelector(store => store.authentication),
    { signupFormData, formErrors, isLoading, isSubmitting } = useSelector(store => store.signup),
    dispatch = useDispatch()

  if (authenticated) return <Redirect to='/dashboard' />

  const handleFormChange = (name, value) => {
    dispatch(signupChange(name, value))
  }

  const handleSubmit = event => {
    event.preventDefault()
    dispatch(signUp())
  }

  const _onChange = e => {
    handleFormChange(e.target.name, e.target.value)
  }

  return (
    <UILoader blocking={isLoading}>
      <div className="auth-wrapper auth-v1 px-2">
        <div className="auth-inner py-2">
          <Card className="mb-0">
            <CardBody>
              <Link
                className="brand-logo"
                to="/"
                onClick={(e) => e.preventDefault()}
              >
                <h2 className="brand-text text-primary ml-1">Carhoot</h2>
              </Link>
              <CardTitle tag="h4" className="mb-1">
                Create an account
              </CardTitle>
              <Form
                className="auth-signup-form mt-2"
                onSubmit={handleSubmit}
              >
                <InputGroup
                  type={'text'}
                  label={'First Name'}
                  error={formErrors['firstName']}
                  name={'firstName'}
                  defaultValue={signupFormData.firstName}
                  placeholder={'Please Enter Your First Name'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                  autoFocus
                />
                <InputGroup
                  type={'text'}
                  label={'Last Name'}
                  error={formErrors['lastName']}
                  name={'lastName'}
                  defaultValue={signupFormData.lastName}
                  placeholder={'Please Enter Your Last Name'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                />
                <InputGroup
                  type={'text'}
                  label={'User Name'}
                  error={formErrors['userName']}
                  name={'userName'}
                  defaultValue={signupFormData.userName}
                  placeholder={'Please Enter Your User Name'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                />
                <InputGroup
                  type={'select'}
                  label={'Country Code'}
                  error={formErrors['countryCode']}
                  name={'countryCode'}
                  defaultValue={signupFormData.countryCode}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                  required={true}
                >
                  <option value='255'>Select Country Code</option>
                  <option value='254'>+254 Kenya</option>
                </InputGroup>
                <InputGroup
                  type={'number'}
                  label={'Phone Number'}
                  error={formErrors['phoneNumber']}
                  name={'phoneNumber'}
                  defaultValue={signupFormData.phoneNumber}
                  placeholder={'Please Enter Your Phone Number'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                />
                <InputGroup
                  type={'select'}
                  label={'Employee Role'}
                  error={formErrors['role']}
                  name={'role'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                  defaultValue={signupFormData.role}
                >
                  <option value=''>Select Role</option>
                  <option value='operations'>Operations</option>
                </InputGroup>
                <InputGroup
                  type={'email'}
                  label={'Email'}
                  error={formErrors['email']}
                  name={'email'}
                  defaultValue={signupFormData.email}
                  placeholder={'Please Enter Your Email'}
                  onInputChange={(name, value) => {
                    handleFormChange(name, value)
                  }}
                />
                <FormGroup>
                  <div className="d-flex justify-content-between">
                    <Label className="form-label" for="signup-password">
                      Password
                    </Label>
                  </div>
                  <InputPasswordToggle
                    className="input-group-merge"
                    id="signup-password"
                    name={'password'}
                    placeholder={'Please Enter Your Password'}
                    value={signupFormData.password}
                    autoComplete={'current-password'}
                    onChange={(e) => {
                      _onChange(e)
                    }}
                  />
                  <span className="text-danger">{formErrors['password'] && formErrors['password'][0]}</span>
                </FormGroup>
                <Button.Ripple color="primary" block disabled={isSubmitting} type={'submit'}>
                  Sign Up
                </Button.Ripple>
                <small className='mt-3'>Do you have an account? <Link to='/login'>Login</Link></small>
              </Form>
            </CardBody>
          </Card>
        </div>
      </div>
    </UILoader>
  )
}

export default SignUp
